import React from 'react';
import { Button, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle, useMediaQuery, useTheme } from '@material-ui/core';

interface ResponsiveDialogProps {
  isOpen: boolean;
  title: string;
  description?: string;
  acceptButtonText: string;
  cancelButtonText: string;
  closeModal: (e: any) => void;
}

export default function ConfirmationDialog({
  isOpen,
  title,
  description,
  acceptButtonText,
  cancelButtonText,
  closeModal
}: ResponsiveDialogProps) {
  const [open, setOpen] = React.useState(false);
  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down('sm'));

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  React.useEffect(() => {
    if (isOpen) {
      handleOpen();
    } else {
      handleClose();
    }
  }, [isOpen]);

  return (
    <Dialog
      fullScreen={fullScreen}
      open={open}
      onClose={() => closeModal(false)}
      aria-labelledby="responsive-dialog-title"
    >
      <DialogTitle id="responsive-dialog-title">{title}</DialogTitle>
      {
        description ? <DialogContent>
          <DialogContentText>
            {description}
          </DialogContentText>
        </DialogContent> : null
      }
      <DialogActions>
        <Button autoFocus onClick={() => closeModal(true)} color="primary">
          {acceptButtonText}
        </Button>
        <Button onClick={() => closeModal(false)} color="primary" autoFocus>
          {cancelButtonText}
        </Button>
      </DialogActions>
    </Dialog>
  );
}
