import React, { Component } from 'react';
import { Box, IconButton, Theme, withStyles } from '@material-ui/core';
import { connect } from 'react-redux';
import { IShortNote, StylesDictionary } from '../../types';
import { Delete, Edit } from '@material-ui/icons';
import ConfirmationDialog from '../common/ConfirmationDialog';
import { removeNote } from '../../store/actions/notesActions';
import notesReducer from '../../store/reducers/notesReducer';
import { Link } from 'react-router-dom';

const useStyles = (theme: Theme): StylesDictionary => ({
  root: {
    display: 'flex',
    flexWrap: 'wrap',
    paddingLeft: '10px',
  },
  buttonActions: {
    width: '28px',
  },
});

interface NoteProps {
  classes: Record<string, string>,
  note: IShortNote,
  removeNote: Function,
}

interface NoteState {
  confirmDialog: boolean,
}

class Note extends Component<NoteProps, NoteState> {
  state = {
    confirmDialog: false,
  };

  constructor(props: NoteProps) {
    super(props);
  }

  closeModal(isAccepted: boolean) {
    if (isAccepted) {
      this.props.removeNote(this.props.note);
    }

    this.setState({
      confirmDialog: false,
    });
  }

  openRemoveConfirmationDialog(note: IShortNote) {
    this.setState({
      confirmDialog: true,
    });
  }

  render() {
    const { classes, note } = this.props;
    return [
      <Box display="flex" flexDirection="row" flexWrap="wrap" justifyContent="space-between" px={2}>
        <p>{note.id}</p>
        <Box display="flex" flexDirection="row" alignItems="center">
          <Link
            to={{
              pathname: `/note/${note.id}`,
            }}
          >
            <IconButton
              className={classes.buttonActions}
              size="small"
              component="span"
            >
              <Edit fontSize="small" />
            </IconButton>
          </Link>
          <IconButton
            className={classes.buttonActions}
            size="small"
            component="span"
            onClick={() => this.openRemoveConfirmationDialog(note)}
          >
            <Delete fontSize="small" />
          </IconButton>
        </Box>
      </Box>,

      <ConfirmationDialog
        isOpen={this.state.confirmDialog}
        title="Are you sure you want to remove this?"
        acceptButtonText="Yes"
        cancelButtonText="No"
        closeModal={this.closeModal.bind(this)}
      />];
  }
}

const mapState = notesReducer;

const mapDispatch = {
  removeNote: removeNote,
};

export default connect(mapState, mapDispatch)(withStyles(useStyles)(Note));
