import {
  INotesStore,
  ENotesActions,
} from '../../types/store';

const initialState: INotesStore = {
  notesShortList: {},
  currentNote: null,
  curHighLevelBullets: null,
};

const authReducer = (state = initialState, action: any): INotesStore => {
  switch (action.type) {
    case ENotesActions.GET_NOTES_SHORT_LIST:
      return {
        ...state,
        notesShortList: action.payload,
      };
    case ENotesActions.GET_PARTICULAR_NOTE:
      return {
        ...state,
        currentNote: action.payload.note,
        curHighLevelBullets: action.payload.highelevelBullets,
      };
    case ENotesActions.CHANGE_BULLET_STATUS:
      return {
        ...state,
        currentNote: {
          ...state.currentNote,
          [action.payload.id]: action.payload,
        },
      };
    case ENotesActions.REMOVE_BULLET:
      const note = state.currentNote ? state.currentNote : {};
      if (Array.isArray(action.payload.bullets) && action.payload.bullets.length) {
        for (const bulletKey of action.payload.bullets) {
          delete note[bulletKey];
        }
      }
      delete note[action.payload.id];

      return {
        ...state,
        currentNote: Object.keys(note).length ? note : null,
      };
    case ENotesActions.CLEAN_PARTICULAR_NOTE:
      return {
        ...state,
        currentNote: null,
        curHighLevelBullets: null,
      };
    case ENotesActions.REMOVE_NOTE:
      const notesList = { ...state.notesShortList };
      delete notesList[action.payload?.id];
      return {
        ...state,
        notesShortList: notesList,
      };
    case ENotesActions.SAVE_NOTE:
    default:
      return state;
  }
};

export default authReducer;
