import React, { Component } from 'react';
import { Box, GridList, GridListTile, IconButton, Theme, withStyles } from '@material-ui/core';
import { connect } from 'react-redux';
import { INotesStore, IShortNote, StylesDictionary } from '../../types';
import './NoteList.css';
import NoteShort from './NoteShort';
import { Add } from '@material-ui/icons';
import notesReducer from '../../store/reducers/notesReducer';
import { getNotesList, removeNote } from '../../store/actions/notesActions';

const useStyles = (theme: Theme): StylesDictionary => ({
  list: {
    width: '50%',
    padding: '10px',
  },
  buttonAdd: {
    width: '32px',
  },
});

interface NoteListProps {
  classes: Record<string, string>,
  notes: INotesStore
  getNotesList: Function,
}

interface NoteListState { }

class NoteList extends Component<NoteListProps, NoteListState> {
  constructor(props: NoteListProps) {
    super(props);
    this.props.getNotesList();
  }

  addNote() {
    console.log('>>>>>>>>>>>>. clicked add note ', this.props.notes.notesShortList);
  }

  render() {
    const { classes, notes } = this.props;
    const notesList = notes.notesShortList;

    return (
      <Box display="flex" alignItems="center" flexDirection="column" p={10}>
        <IconButton
          className={classes.buttonAdd}
          aria-label="upload picture"
          size="small"
          component="span"
          onClick={() => this.addNote()}
        >
          <Add />
        </IconButton>

        <GridList cellHeight={50} className={classes.list} cols={1}>
          {Object.keys(notesList).map((key, index) => (
            <GridListTile key={key + index} className="tile">
              <NoteShort note={notesList[key]} />
            </GridListTile>
          ))}
        </GridList>
      </Box>
    );
  }
}

const mapState = notesReducer;

const mapDispatch = {
  getNotesList: getNotesList,
}

export default connect(mapState, mapDispatch)(withStyles(useStyles)(NoteList));
