import React, { Component } from 'react';
import { Box, IconButton, Theme, withStyles } from '@material-ui/core';
import { connect } from 'react-redux';
import { INotesStore, StylesDictionary } from '../../types';
import { cleanParticularNote, getParticularNote, saveNote } from '../../store/actions/notesActions';
import notesReducer from '../../store/reducers/notesReducer';
import Bullets from './Bullet';
import { ArrowBack, Save } from '@material-ui/icons';

const useStyles = (theme: Theme): StylesDictionary => ({
  root: {
    textAlign: 'left',
  },
});

interface NoteProps {
  classes: Record<string, string>,
  match: any,
  notes: INotesStore,
  history: any,
  getNote: Function,
  cleanNote: Function,
  saveNote: Function,
}

interface NoteState { }

class Note extends Component<NoteProps, NoteState> {

  constructor(props: NoteProps) {
    super(props);
    this.props.getNote(props.match.params.id);
  }

  goBack() {
    this.props.cleanNote();
    this.props.history.push('/notes');
  }

  render() {
    const { classes, notes } = this.props;
    return (
      <div className={classes.root}>
        <Box display="flex" flexDirection="row" >
          <IconButton
            className={classes.buttonActions}
            size="small"
            component="span"
            onClick={() => this.goBack()}
          >
            <ArrowBack fontSize="small" />
          </IconButton>
          <IconButton
            className={classes.buttonActions}
            size="small"
            component="span"
            onClick={() => this.props.saveNote({ note: notes.currentNote, curHighLevelBullets: notes.curHighLevelBullets })}
          >
            <Save fontSize="small" />
          </IconButton>
        </Box>
        { notes.curHighLevelBullets && notes.currentNote ? <Bullets list={notes.curHighLevelBullets} /> : 'Note is empty'}
      </div>
    );
  }

  // componentWillUnmount() {
  //   this.props.cleanParticularNote();
  // }
}

const mapState = notesReducer;

const mapDispatch = {
  getNote: getParticularNote,
  cleanNote: cleanParticularNote,
  saveNote: saveNote,
}

export default connect(mapState, mapDispatch)(withStyles(useStyles)(Note));