import {
  IAuthStore,
  EAuthActions,
} from '../../types/store/AuthStore';

const initialState: IAuthStore = {
  token: '',
  identifier: '',
};

const authReducer = (state = initialState, action: any): IAuthStore => {
  switch (action.type) {
    case EAuthActions.AUTHENTICATE:
      return {
        ...state,
        ...action.payload,
      };
    default:
      return state;
  }
};

export default authReducer;
