import React, { Component } from 'react';
import './LoginForm.css';
import { Box, Button, TextField, Theme, withStyles } from '@material-ui/core';
import { connect } from 'react-redux';
import authReducer from '../../store/reducers/authReducer';
import { authenticateAction } from '../../store/actions/authActions';
import { StylesDictionary } from '../../types';

const useStyles = (theme: Theme): StylesDictionary => ({
  root: {
    minHeight: '100vh',
    fontSize: 'calc(10px + 2vmin)',
  },
  form: {
    display: 'flex',
    flexWrap: 'wrap',
    boxShadow: '0 0 10px rgba(0,0,0,0.5)',
  },
  textField: {
    margin: theme.spacing(1),
    width: '15ch',
  },
  button: {
    marginTop: theme.spacing(1),
    marginLeft: theme.spacing(2),
    marginRight: theme.spacing(1),
    marginBottom: theme.spacing(2),
  },
});

interface LoginFormProps {
  classes: Record<string, string>,
  authenticateAction?: any,
}

interface LoginFormState {
  email: string,
  password: string,
}

class LoginForm extends Component<LoginFormProps, LoginFormState> {
  state = {
    email: '',
    password: ''
  };

  constructor(props: LoginFormProps) {
    super(props);
    this.handleChange = this.handleChange.bind(this);
    this.submitForm = this.submitForm.bind(this);
  }

  handleChange(e: any) {
    e.currentTarget.id === 'email' ?
      this.setState({ email: e.currentTarget.value }) :
      this.setState({ password: e.currentTarget.value });
  }

  submitForm(e: any) {
    const { email, password } = this.state;
    this.props.authenticateAction({ email, password });
  }

  render() {
    const { classes } = this.props;
    return (
      <Box display="flex" alignItems="center" justifyContent="center" flexDirection="column" className={classes.root}>
        <div className={classes.form}>
          <form className="form">
            <TextField
              id="email"
              label="Login"
              type="email"
              className={classes.textField}
              onChange={this.handleChange}
            />
            <TextField
              id="password"
              label="Password"
              type="password"
              className={classes.textField}
              onChange={this.handleChange}
            />

            <Button
              color="primary"
              variant="contained"
              className={classes.button}
              onClick={this.submitForm}
            >
              Login
            </Button>
          </form>
        </div>
      </Box>
    );
  }
}

const mapState = authReducer;

const mapDispatch = {
  authenticateAction
}

export default connect(mapState, mapDispatch)(withStyles(useStyles)(LoginForm));