import { IBullet, INote, IShortNote } from '../../types';
import { ENotesActions } from '../../types/store';

export function getNotesList() {
  console.log('>>>>>>>>>> get notes list api call ');
  const notes = {
    '1': {
      id: '1',
    },
    '2': {
      id: '2',
    },
    '3': {
      id: '3',
    },
    '4': {
      id: '4',
    },
  };
  return {
    type: ENotesActions.GET_NOTES_SHORT_LIST,
    payload: notes,
  };
}

export function removeNote(note: IShortNote | INote) {
  console.log('>>>>>>>>>> remove note api call', note)
  return {
    type: ENotesActions.REMOVE_NOTE,
    payload: note,
  };
}

export function getParticularNote(shortNote: IShortNote) {
  console.log('>>>>>>>>> get the full note with bullets by note id', shortNote);

  const note: INote = {
    '11': {
      id: '11',
      isChecked: false,
      label: 'Test 1',
      highlevel: true,
      bullets: ['12', '13', '15'],
    },
    '12': {
      id: '12',
      isChecked: true,
      label: 'Test 2',
    },
    '13': {
      id: '13',
      isChecked: false,
      label: 'Test 3',
      bullets: [],
    },
    '14': {
      id: '14',
      isChecked: true,
      highlevel: true,
      label: 'Test 4'
    },
    '15': {
      id: '15',
      isChecked: false,
      label: 'Test 5',
      bullets: ['16']
    },
    '16': {
      id: '16',
      isChecked: true,
      label: 'Test 6',
    }
  };

  const highelevelBullets = Object.keys(note).map((key) => (note[key].highlevel ? key : null)).filter((el) => el != null);
  const fullNote = {
    note,
    highelevelBullets,
  };

  return {
    type: ENotesActions.GET_PARTICULAR_NOTE,
    payload: fullNote,
  };
}

export function changeBullet(bullet: IBullet) {
  return {
    type: ENotesActions.CHANGE_BULLET_STATUS,
    payload: bullet,
  };
}

export function removeBullet(bullet: IBullet) {
  return {
    type: ENotesActions.REMOVE_BULLET,
    payload: bullet,
  }
}

export function saveNote(note: INote) {
  console.log('>>>>>>>>>> save note api call ', note);

  return {
    type: ENotesActions.SAVE_NOTE,
  };
}

export function cleanParticularNote() {
  return {
    type: ENotesActions.CLEAN_PARTICULAR_NOTE,
  };
}
