import React from 'react';
import { connect } from 'react-redux';
import { Box, Checkbox, createStyles, FormControlLabel, IconButton, makeStyles, TextField, Theme } from '@material-ui/core';
import { Add, Check, Close, Delete, Edit, Favorite, FavoriteBorder } from '@material-ui/icons';

import notesReducer from '../../store/reducers/notesReducer';
import { changeBullet, removeBullet } from '../../store/actions/notesActions';
import { IBullet, INotesStore } from '../../types';

interface BulletProps {
  list: string[],
  curBullet?: IBullet,
  notes: INotesStore,
  changeBullet: Function,
  removeBullet: Function,
};

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    buttonActions: {
      width: theme.spacing(4),
    },
  }),
);

function Bullet({ list, curBullet, notes, changeBullet, removeBullet }: BulletProps) {
  const classes = useStyles();
  const [isEditBullet, setEdit] = React.useState('');
  const [bulletName, setBulletName] = React.useState('');

  function handleChange(event: React.ChangeEvent<HTMLInputElement>, bullet: IBullet) {
    changeBullet({ ...bullet, isChecked: event.target.checked });
  }

  function saveChangedBullet(bullet: IBullet) {
    changeBullet({ ...bullet, label: bulletName });
    setEdit('');
    setBulletName('');
  }

  function addBullet(bullet?: IBullet) {
    console.log('>>>>>>>>>> ', bullet)
  }

  function actionButtons(bullet: IBullet) {
    if (!isEditBullet || isEditBullet !== bullet.id) {
      return (
        <Box display="flex" flexDirection="row" alignItems="cener">
          <IconButton
            className={classes.buttonActions}
            size="small"
            component="span"
            onClick={() => { setBulletName(bullet.label); setEdit(bullet.id) }}
          >
            <Edit fontSize="small" />
          </IconButton>
          <IconButton
            className={classes.buttonActions}
            size="small"
            component="span"
            onClick={() => removeBullet(bullet)}
          >
            <Delete fontSize="small" />
          </IconButton>
          <IconButton
            className={classes.buttonActions}
            size="small"
            component="span"
            onClick={() => addBullet(bullet)}
          >
            <Add fontSize="small" />
          </IconButton>
        </Box>
      );
    } else {
      return (
        <Box display="flex" flexDirection="row" alignItems="cener">
          <IconButton
            className={classes.buttonActions}
            size="small"
            component="span"
            onClick={() => saveChangedBullet(bullet)}
          >
            <Check fontSize="small" />
          </IconButton>
          <IconButton
            className={classes.buttonActions}
            size="small"
            component="span"
            onClick={() => { setBulletName(''); setEdit('') }}
          >
            <Close fontSize="small" />
          </IconButton>
        </Box>
      );
    }
  }

  function getBulletLabel(bullet: IBullet) {
    if (isEditBullet && isEditBullet === bullet.id) {
      return (
        <TextField value={bulletName} onChange={(event) => setBulletName(event.target.value)} />
      )
    } else {
      return bullet.label
    }
  }

  function renderCheckBox(bullet: IBullet) {
    return (
      <Box display="flex" flexDirection="row">
        <FormControlLabel
          key={'form-control-label-' + bullet.id}
          control={
            <Checkbox
              icon={<FavoriteBorder />}
              checkedIcon={<Favorite />}
              checked={bullet.isChecked}
              onChange={(event) => handleChange(event, bullet)}
              name="checkedH"
            />
          }
          label={getBulletLabel(bullet)}
        />
        {actionButtons(bullet)}
      </Box>
    );
  }

  function renderBullets(id: string) {
    const bullet = notes.currentNote ? notes.currentNote[id] : {} as IBullet;

    if (bullet) {
      if (bullet.bullets) {
        return (
          [
            renderCheckBox(bullet),
            <Bullet
              key={'form-control-label-' + id}
              list={bullet.bullets}
              notes={notes}
              curBullet={bullet}
              changeBullet={changeBullet}
              removeBullet={removeBullet}
            />
          ]
        )
      } else {
        return (renderCheckBox(bullet));
      }
    } else {
      return null;
    }
  }

  return (
    <Box display="flex" flexDirection="column" flexWrap="wrap" pl={2}>
      {list.map(renderBullets)}
      {/* {
        list.length ?
          <IconButton
            className={classes.buttonActions}
            size="small"
            component="span"
            onClick={() => addBullet(curBullet)}
          >
            <Add fontSize="small" />
          </IconButton> : null
      } */}
    </Box>
  )
}

const mapState = notesReducer;

const mapDispatch = {
  changeBullet: changeBullet,
  removeBullet: removeBullet,
}

export default connect(mapState, mapDispatch)(Bullet);
